<template>
  <header
    class="admin-header"
  >
    <div class="admin-header__body">
      <slot />
    </div>
    <GlobalSearchIcon name="search" @click="setIsSearch" />
  </header>
</template>

<script>
import { useRouter } from '@/shared/plugins/vue-router'
import { useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import GlobalSearchIcon from '@/admin/components/GlobalSearch/GlobalSearchIcon'
import { ROUTES_NAMES } from '@/admin/common/routes'

export default {
  name: 'AdminHeader',
  components: { GlobalSearchIcon },
  setup () {
    const router = useRouter()
    const {
      SET_IS_SEARCH
    } = useMutations(MODULES_NAMES.GLOBAL_SEARCH)

    const setIsSearch = (flag) => {
      SET_IS_SEARCH(flag)
      router.push({
        name: ROUTES_NAMES.GLOBAL_SEARCH
      })
    }
    return {
      setIsSearch
    }
  }
}
</script>

<style lang="scss">
.admin-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid map-get($colors, "default");

  &_search-active {
    border-color: transparent;
  }
  &__body {
    padding: 35px 60px 36px 60px;
  }
}
</style>
